.login__form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: calc(430px - (36px*2));
    min-height: calc(330px - (34px + 37px));
    color: #fff;
    padding-left: 36px;
    padding-right: 36px;
    padding-bottom: 37px;
    padding-top: 34px;
  }
  
  @media screen and (max-width: 600px) {
    .login__form {
      width: calc(282px - (22px*2));
      min-height: calc(322px - (25px*2));
      padding-left: 22px;
      padding-right: 22px;
      padding-bottom: 25px;
      padding-top: 25px;
    }
  }